import React from 'react';
import { BrowserRouter as Router, Route} from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import './App.css';

import Navbar from "./components/navbar.component";
import BookNow from "./components/bookNow.component";
import OurBarbers from "./components/ourbarbers.component";
import Location from "./components/location.component";
import Subscribe from "./components/subscribe.component";
import BookEster from "./components/bookEster.component";
import BookDanny from "./components/bookDanny.component";


function App() {
  return (
    <Router>
    <div className="container">
      <Navbar />
      <br/>
      <Route path="/bookNow" component={BookNow} />
      <Route path="/" exact component={OurBarbers} />
      <Route path="/location" component={Location} />
      <Route path="/subscribe" component={Subscribe} />
      <Route path="/bookEster" component={BookEster} />
      <Route path="/bookDanny" component={BookDanny} />
        <br/>
    </div>

    </Router>
  );
}

export default App;
